<script>
  import { mdiStar, mdiChevronDown, mdiChevronUp } from '@mdi/js'
  import { getMarketplaceOfferings } from '@/api';
  import MarketplaceIcon from '@/assets/icons/marketplace_details.svg';
  import Controls from '@/components/Controls';
  import SearchIcon from '@/assets/icons/search.svg';

  export default {
    name: "Marketplace",
    components: {
      MarketplaceIcon,
      Controls,
      SearchIcon,
    },
    data() {
      return {
        offerings: [],
        offeringType: [],
        category: [],
        provider: [],
        rate: [],
        icons: {
          star: mdiStar,
          arrowUp: mdiChevronUp,
          arrowDown: mdiChevronDown,
        },
        isCategoryVisible: false,
        isProviderVisible: false,
        search: '',
      }
    },
    computed: {
      searchOfferings() {
        return this.offerings.filter((offering) => offering.title.toLowerCase().includes(this.search.toLowerCase()));
      },
      filterByType() {
        if (!this.offeringType.length) {
          return this.searchOfferings;
        }

        return this.searchOfferings.filter((item) => {
          if (this.offeringType.includes('visual analytics') && this.offeringType.includes('dataset')) {
            return item.va || !item.va;
          }

          if (this.offeringType.includes('visual analytics')) {
            return item.va;
          }

          if (this.offeringType.includes('dataset')) {
            return !item.va;
          }
        });
      },
    },
    created() {
      getMarketplaceOfferings()
        .then(({ data }) => this.offerings = data)
        .catch((error) => console.error(error))
    },
  }
</script>

