      padding: 10px;
      color: #0a1448;
      position: absolute;
      right: 0;
      z-index: 1;

      .notification {
        border-radius: 4px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 10px;
        padding: 10px;
        box-shadow: 0 13px 12px 0 #eaedf4;
        background-color: #ffffff;

        &.active {
          background-color: rgba(0, 114, 255, 0.2);
        }

        .mark-text {
          font-size: 12px;
