<template>
  <div>
    <div class="header">
      <div class="title-container">
        <div class="icon-container">
          <DashboardIcon class="icon" />
        </div>
        <h1>Dashboard</h1>
        <Controls />
      </div>
      <v-tabs class="tabs" v-model="tab" slider-color="#2f6bff" slider-size="3" color="#0a1448">
        <v-tab key="projects">Projects</v-tab>
        <v-tab key="items">Items</v-tab>
      </v-tabs>
    </div>
    <div class="content">
      <v-tabs-items v-model="tab" class="tabs-items">
        <v-tab-item key="projects">
          <Projects />
        </v-tab-item>
        <v-tab-item key="items">
          <Items />
        </v-tab-item>
      </v-tabs-items>
    </div>
  </div>
</template>

<script>
  import DashboardIcon from '@/assets/icons/dashboard.svg';
  import Controls from '@/components/Controls';
  import Items from './Items';
  import Projects from './Projects';
  import { getMyIP, getTableauToken } from '@/api';

  export default {
    name: "Dashboard",
    components: {
      Items,
      DashboardIcon,
      Controls,
      Projects,
    },
    data() {
      return {
        tab: null,
      }
    },
    mounted() {
      getMyIP()
        .then(response => {
          getTableauToken(response.data)
            .then(({ status, data }) => {
              if (status === 200) {
                data.key && localStorage.setItem('key', data.key);
              }
            });
        })
        .catch(error => console.error(error));
    }
  }
</script>

