<template>
  <div class="register-container">
    <form @submit="handleSubmit">
      <div class="form-header">
        <h1 class="title">Sign Up</h1>
      </div>
      <div class="form-content">
        <div class="form-group">
          <label for="username">Name</label>
          <input
            type="text"
            name="username"
            class="field"
            placeholder="ex. John Brown"
            id="username"
            v-model="username"
          >
        </div>
        <div class="form-group">
          <label for="email">Email address</label>
          <input type="email" name="email" class="field" id="email" v-model="email">
        </div>
        <div class="form-group">
          <label for="password">Password</label>
          <input type="password" name="password" class="field" id="password" v-model="password">
        </div>
        <div class="form-group">
          <label for="confirmPassword">Repeat password</label>
          <input type="password" name="confirmPassword" class="field" id="confirmPassword" v-model="confirmPassword">
        </div>
      </div>
      <div class="form-footer">
        <button type="submit" class="submit-button">Create account</button>
      </div>
    </form>
  </div>
</template>

<script>
  import { register } from '@/api';

  export default {
    name: "Register",
    data() {
      return {
        username: '',
        email: '',
        password: '',
        confirmPassword: '',
      }
    },
    methods: {
      handleSubmit(event) {
        event.preventDefault();
        register({ username: this.username, email: this.email, password: this.password })
          .then(({ data }) => {
            data.result && this.$router.push('/');
          })
          .catch(error => {
            console.error(error)
          });
      },
    }
  }
</script>

