<template>
  <div class="login-container">
    <form @submit="handleSubmit">
      <div class="form-header">
        <h1 class="title">Log in</h1>
      </div>
      <div class="form-content">
        <div class="form-group">
          <label for="email">Login</label>
          <input
            type="text"
            name="email"
            class="field"
            :class="{ 'error-input': $v.email.$error }"
            id="email"
            v-model.trim="$v.email.$model"
          >
          <div v-if="error" class="error-label">{{ error }}</div>
          <div v-if="!$v.email.required" class="error-label">Field is required</div>
        </div>
        <div class="form-group">
          <label for="password">Password</label>
          <input
            type="password"
            name="password"
            class="field"
            :class="{ 'error-input': $v.password.$error }"
            id="password"
            v-model.trim="$v.password.$model"
          >
          <div v-if="!$v.password.required" class="error-label">Field is required</div>
        </div>
      </div>
      <div class="form-footer">
        <button type="submit" class="submit-button" :disabled="isLoading">
          <span v-if="isLoading">Loading...</span>
          <span v-else>Log in</span>
        </button>
      </div>
    </form>
  </div>
</template>

<script>
  import { required } from 'vuelidate/lib/validators'
  import { login } from '@/api';

  export default {
    name: "Login",
    data() {
      return {
        email: 'root',
        password: 'w2w2w2W@',
        error: '',
        isLoading: false,
      }
    },
    methods: {
      handleSubmit(event) {
        event.preventDefault();

        this.isLoading = true;
        login({ email: this.email, password: this.password })
          .then(({ status, data }) => {
            this.isLoading = false;
            if (status === 200) {
              data.access && localStorage.setItem('token', data.access);
              this.$router.push('/');
            }
          })
          .catch(() => {
            this.isLoading = false;
            this.error = 'Invalid credentials';
          });
      },
    },
    watch: {
      email() {
        this.error = '';
      },
      password() {
        this.error = '';
      }
    },
    validations: {
      email: {
        required,
      },
      password: {
        required,
      },
    }
  }
</script>

