<template>
  <div class="profile" @click="active = true" v-click-outside="onClickOutside">
    <span class="name">Alice Kowalewsky</span>
    <img class="avatar" src="@/assets/avatar.png" alt="avatar">
    <div v-if="active" class="content">
      <router-link :to="{ name: 'profile'}" tag="div" class="item">
        Profile
      </router-link>
      <v-divider></v-divider>
      <div @click="handleLogout" class="item">Logout</div>
    </div>
  </div>
</template>

<script>
  import vClickOutside from 'v-click-outside';
  import { clearToken } from '@/helpers';

  export default {
    name: "Profile",
    directives: {
      clickOutside: vClickOutside.directive,
    },
    data() {
      return {
        active: false,
      }
    },
    methods: {
      onClickOutside () {
        this.active = false;
      },
      handleLogout() {
        this.$router.push({ name: 'login' });
        clearToken();
      }
    }
  }
</script>

